import bg11Image from 'assets/img/bg/background_1920-11.jpg';
import landingImage from 'assets/img/logo/landing-img.png';
import bg18Image from 'assets/img/bg/background_1920-18.jpg';
import bg1Image from 'assets/img/bg/background_640-1.jpg';
import bg3Image from 'assets/img/bg/background_640-3.jpg';
import user1Image from 'assets/img/users/100_1.png';
import { UserCard } from 'components/Card';
import Page from 'components/Page';
import React from 'react';

import PageSpinner from 'components/PageSpinner';

import AsyncStorage from '@callstack/async-storage';
import { returningUser } from "../../api/api";

import {
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Button,
  Card,
  CardBody,
  CardImg,
  CardImgOverlay,
  CardLink,
  CardText,
  CardTitle,
  Col,
  ListGroup,
  ListGroupItem,
  Alert,
  Row,
} from 'reactstrap';
import config from '../../api/config';

// const EntryPage = () => {
class EntryPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            account_no:'',
            is_account:'1',
            tlabel:'Use phone number instead',
            msg: '',
            error: '',
            preload:false,
        };
    }

    handleAccountInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, account_no: v,});
    }
    handleTypeInput = (event) => {
        const v = event.target.checked;
        if(v === true)
        {
            this.setState({...this.state, is_account: '0', account_no:'',tlabel:'Uncheck this to use Account number',});
        }else
        {
            this.setState({...this.state, is_account: '1', account_no:'',tlabel:'Use phone number instead',});
        }
        return;
    }
    handlePhoneInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, account_no: v,});
    }
    handleSignIn = () => {
        let postData = {
            account_no: this.state.account_no,
            is_account: this.state.is_account,
        }
        this.setState({ ...this.state, preload:true});
        returningUser(postData)
        .then((response) => {
            if( response.status === 200 )
            {
                AsyncStorage.setItem(config.secret, JSON.stringify(response)).then(() => {}).catch((err) =>{});
                window.location.href = '/';
                this.setState({ 
                    ...this.state, 
                    preload:false,
                });
            }
            else
            {
                this.setState({ 
                    ...this.state, 
                    preload:false,
                    error: response.message,
                    msg:'',
                });
                return;
            }
        })
        .catch((error) => {
            this.setState({ 
                ...this.state, 
                preload:false,
                error: 'Connection error occured. Try again',
                msg:'',
            });
            return;
        });
    }
    render() {
        return (
        <Page title="Cards" breadcrumbs={[{ name: 'cards', active: true }]}>
        <Row>
            <Col md={7} sm={7} xs={12} className="mb-3">
            <Card className="flex-row text-center card-uncurved">
                <CardBody>
                <CardTitle><h1><b>M-Lugha</b> on the Web</h1></CardTitle>
                {this.state.preload && (
                    <CardTitle><PageSpinner/></CardTitle>
                )}
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
                <br></br>
                    <div className="row justify-content-center">
                        <div className="col-md-8 col-sm-8 col-xs-12">
                            <Form>
                                { parseInt(this.state.is_account) === 1 && (
                                    <FormGroup>
                                        <Input 
                                            onChange={this.handleAccountInput} 
                                            value={this.state.account_no} 
                                            type="number" 
                                            name="acc" 
                                            placeholder="enter 10 digit account number"/>
                                    </FormGroup>
                                )}
                                <FormGroup check inline>
                                    <Label check>
                                        <Input 
                                            onChange={this.handleTypeInput} 
                                            value={this.state.is_account}
                                            type="checkbox" /> {this.state.tlabel}
                                    </Label>
                                </FormGroup>
                                { parseInt(this.state.is_account) === 0 && (
                                    <div>
                                        <hr></hr>
                                        <FormGroup>
                                            <Input
                                                onChange={this.handlePhoneInput} 
                                                value={this.state.account_no}
                                                type="number"
                                                name="phone"
                                                placeholder="enter guardian phone number"
                                            />
                                        </FormGroup>
                                    </div>
                                )}
                                <br></br>
                                <br></br>
                                <div className="row">
                                    <div className="col-md-12 col-sm-12 col-xs-12">
                                        <Button onClick={this.handleSignIn} className="btn-custom">Sign In</Button>
                                    </div>
                                    <div className="col-md-12 col-sm-12 col-xs-12">
                                        <a href="/new/account" className="btn-custom bones btn btn-secondary">Sign Up Here</a>
                                    </div>
                                </div>
                            </Form>
                        </div>
                    </div>
                </CardBody>
            </Card>
            </Col>
            <Col md={5} sm={5} xs={12} className="mb-0">
                <img width={450} src={landingImage}/>
            </Col>
        </Row>
        </Page>
    );
 }
};
export default EntryPage;
